import * as THREE from "three";
// 
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
// console.log(THREE);
// 
// 
const scene = new THREE.Scene();

// 
const camera = new THREE.PerspectiveCamera(
  75, // 
  window.innerWidth / window.innerHeight, // 
  0.1, // 
  1000 // 
);
// 
camera.position.set(0, 0, 10);

scene.add(camera);

// 
// 
const cubeGeometry = new THREE.BoxGeometry(1, 1, 1);
const cubeMateril = new THREE.MeshBasicMaterial({ color: 0xffff00 });
// 
const cube = new THREE.Mesh(cubeGeometry, cubeMateril);

const edges = new THREE.EdgesGeometry(cubeGeometry);
// 
const edgesMaterial = new THREE.LineBasicMaterial({
  color: 0x000000,
});
var line = new THREE.LineSegments(edges, edgesMaterial);
// 

// 

// 
// cube.scale.set(1, 2, 3);
// line.scale.set(1, 2, 3);
// 
// 
cube.rotation.set(0.5, 0.5, 0.5);
line.rotation.set(0.5, 0.5, 0.5);
scene.add(cube, line);
// 
const render = new THREE.WebGLRenderer({ antialias: true });
// 
render.setSize(window.innerWidth, window.innerHeight);
// webglcanvasbody
document.body.appendChild(render.domElement);
// 

render.render(scene, camera);

// 
const controls = new OrbitControls(camera, render.domElement);

// 
const axesHelper = new THREE.AxesHelper(5);
scene.add(axesHelper);
function handleRender() {
  cube.position.x += 0.01;
  cube.rotation.x += 0.01;
  line.position.x += 0.01;
  line.rotation.x += 0.01;
  if (cube.position.x > 5) {
    cube.position.x = 0;
    line.position.x = 0;
  }
  render.render(scene, camera);
  //   
  requestAnimationFrame(handleRender);
}
handleRender();
